.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(50, 50, 50, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 91;
    backdrop-filter: blur(10px);
}

.modalContent {
    position: absolute;
    z-index: 92;
    flex-direction: column;
    overflow: hidden;
    padding: 0rem 1.6rem;
    border-radius: 30px;
    background-color: #0f1110;
    width: 80vw;
    height: fit-content;
    overflow: hidden;
    overflow-y: scroll;
    -ms-overflow-style: none;
    scrollbar-width: none;
    box-shadow: 0px 0px 26px rgba(0, 0, 0, 0.4);
    max-height: 90vh;
}

.modalContent::-webkit-scrollbar {
    display: none;
}

.cardContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.header {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: row;
    width: 100%;
    height: 5vh;
    gap: 1rem;
}

.header > * {
    height: 100%;
}

.titleContainer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
}

.buttonsContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 1rem;
}

.title,
.exitText {
    color: #b7bab6;
    font-family: Poppins;
    font-size: 24px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.tableClubsDetails,
.tableContainerDetails,
.tableContainer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
}

.tableContainerDetails {
    width: 100%;
}

.tableClubsDetails {
    margin-top: 3rem;
    width: 100%;
    align-items: flex-end;
    overflow-y: scroll;
    overflow-x: hidden;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.tableClubsDetails::-webkit-scrollbar {
    display: none;
}

.contentContainer {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    overflow: hidden;
    width: 100%;
}

.contentTitleContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 20vw;
}

.contentDetailsContainer {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    width: 80vw;
    padding: 2rem 4rem;
}

.verticalLine {
    border-left: 1px solid #335c58;
}

.horizontalLine {
    border-bottom: 1px solid #335c58;
    width: stretch;
}

.text,
.category,
.details {
    color: #b7bab6;
    font-family: Poppins;
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: -0.14px;
}

._ {
    display: none;
}

.athletesContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    gap: 4rem;
    width: 100%;
}

@media screen and (max-width: 667px) {
    .modalContent {
        width: 95vw;
        height: 100vh;
        max-height: 100vh;
    }
    .header {
        height: 14vh;
    }
    .contentTitleContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 35vw;
    }
    .contentDetailsContainer {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;
        width: 65vw;
        padding: 2rem 2rem;
    }
    .buttonsContainer {
        flex-direction: column;
    }
}
